body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  margin: 0;
  padding: 0;
  font-family: monospace;
  user-select: none;
}
html, body, #root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.crosshair {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 999;
  color: rgb(0, 255, 0);
  transform: translate(-50%, -50%);
}
.window {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.gui {
  padding: 10px;
  left: 5px;
  z-index: 9999;
  display: flex;
  position: absolute;
  flex-direction: row;
  bottom: 5px;
  gap: 10px;
}
.spawnmenu {
  width: 85%;
  height: 85%;
  position: absolute;
  border-radius: 10px;
  border: 2px solid grey;
  backdrop-filter: blur(10px);
  z-index: 99999;
  padding: 20px;
  gap: 20px;
  display: grid;

}
.object {
  width: 100px;
  height: 100px;
  border: 2px solid gray;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5); 
}
.object figcaption {
  font-size: 20px;
}
.object:hover {
  scale: 1.05;
}
.object:active {
  scale: 0.95;
}
.viewmodel, .game {
  position: absolute !important;
  width: 100% !important;
  height: 100% !important;
}
.status {
  background-color: rgba(255, 215, 0, 0.1);
  padding: 10px;
  display: flex;
  border-radius: 10px;
  flex-direction: row;
  align-items: flex-end;
  gap: 10px;
  box-shadow: 0 0 5px;
}
.status small {
  font-size: 20px;
  text-shadow: 0 0 20px;
}
.status h2 {
  font-size: 50px;
  color: hsl(51, 100%, 40%);
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(255, 215, 0, 0.2);
}
.screen {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
video {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: -3;
}
.volume-control {
  position: absolute;
  top: 1em;
  right: 1em;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5px;
  border-radius: 5px;
}
#volumeSlider {
  width: 100px;
  cursor: pointer;
  background-color: #ddd;
  border-radius: 5px;
  -webkit-appearance: none;
}
#volumeSlider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
}
input[type="range"] {
  width: 150px;
}
.language-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: rgba(255, 255, 255, 0.3);
  color: #333;
  border: none;
  border-radius: 50px;
  width: 50px;
  height: 50px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  transition: background-color 0.3s ease-in-out, transform 0.2s, backdrop-filter 0.3s ease-in-out;
}
.language-button:hover {
  background-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.1); 
  backdrop-filter: blur(12px);
}
.language-button:active {
  transform: scale(0.95);
}
audio {
  display: none;
}
.menu {
  color: #fff;
  margin-left: 4em;
  text-transform: uppercase;
  position: absolute;
  top: 25%;
  left: 5%;
  transform: translateY(0); 
}
.menu h1 {
  font-size: 110px;
  letter-spacing: 10px;
  margin-bottom: 40px;
}
.menu h3 {
  font-weight: 300;
  font-size: 1.9em;
  text-align: center;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.title{
  font-size: 100px;
  font-family: "Lacquer", system-ui;
  font-weight: 400;
  font-style: normal;  
}
.menu ul {
  list-style: none;
  padding: 20px;
  margin: 0;
}
.menu li {
  margin: 1.5em 0;
}
.menu p {
  text-decoration: none;
  color: #fff;
  font-weight: 700;
  font-size: 1.9em;
  display: inline-block;
  transition: transform 0.3s ease, color 0.3s ease;
}
.menu a:hover {
  color: #f0a500;
  transform: scale(1.1);
}
.menu a:active {
  transform: scale(0.95);
}
.language-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: rgba(255, 255, 255, 0.3);
  color: #333;
  border: none;
  border-radius: 50px;
  width: 50px;
  height: 50px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  transition: transform 0.2s ease, background-color 0.3s ease, backdrop-filter 0.3s ease;
}
.language-button:hover {
  background-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.2);
  backdrop-filter: blur(12px);
}
.language-button:active {
  transform: scale(0.9);
}
.volume-control i {
  transition: transform 0.3s ease, color 0.3s ease;
}
.volume-control i:hover {
  color: #f0a500;
  transform: scale(1.2);
}
.volume-control input {
  transition: background-color 0.3s ease;
}
.volume-control input:hover {
  background-color: #f0a500;
}
#sound {
  position: absolute;
  right: 1em;
  top: 1em;
  margin: 0;
  color: #fff;
  cursor: pointer;
}
.menu li a:hover,
#sound:hover {
  color: #dbdbdb;
}
@media screen and (max-width: 800px) {
  .menu h1 {
      font-size: 2em;
  }
  .menu h3 {
      font-size: 1.5em;
  }
}
@media screen and (max-width: 500px) {
  .menu h1 {
      letter-spacing: 5px;
  }
  .menu h3::before,
  .menu h3::after {
      display: none;
  }
}
@media screen and (max-height: 450px) {
  .screen,
  video {
      height: 100vh;
  }
}
